.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 3rem;
  color: #61dafb;
  margin-bottom: 20px;
}

.error {
  color: #ff6b6b;
  font-size: 1.2rem;
  margin: 20px;
  padding: 10px;
  border: 1px solid #ff6b6b;
  border-radius: 5px;
  background-color: rgba(255, 107, 107, 0.1);
}

.server-check {
  margin-top: 30px;
  padding: 15px;
  border: 1px solid #61dafb;
  border-radius: 5px;
  background-color: rgba(97, 218, 251, 0.1);
}

.server-check button {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.server-check button:hover {
  background-color: #4ec9e6;
}